import axios from 'axios';
import React, { useState, useEffect } from 'react'
import { useNavigate, useParams } from 'react-router-dom';
import WaterApiList from '../../../Components/ApiList/WaterApiList';
import BarLoader from '../../../Components/Common/BarLoader';
import moment from 'moment';

const WaterViewPaymentHistory = () => {
    const [applicationFullData, setApplicationFullData] = useState()
    const [readyMadeListData, setReadyMadeListData] = useState("")
    const [isLoading, setIsLoading] = useState(false)
    const [paymentHistory, setPaymentHistory] = useState()

    const { id } = useParams()
    const navigate = useNavigate()

    const { api_waterConsumerPaymentHistory, api_waterApprovedApplicationDetails, header } = WaterApiList();

    const getApplicationDetail = () => {
        setIsLoading(true)
        axios.post(api_waterApprovedApplicationDetails, { id: id }, header)
            .then(function (response) {
                console.log('water Approved Application Details', response.data.data)
                setApplicationFullData(response.data.data)
                setIsLoading(false)
            })
            .catch(function (error) {
                console.log('Error : water Approved Application Details', error)
                setIsLoading(false)
            })
    }

    const getConsumerPaymentHistory = () => {
        setIsLoading(true)
        axios.post(api_waterConsumerPaymentHistory, { consumerId: id }, header)
            .then(function (response) {
                console.log('water payment history', response.data.data)
                setReadyMadeListData(response.data.data)
                setIsLoading(false)
            })
            .catch(function (error) {
                console.log('Error : water payment history', error)
                setIsLoading(false)
            })
    }

    useEffect(() => {
        getApplicationDetail()
        getConsumerPaymentHistory()
    }, [id])


    return (
        <>
            <div className='grid grid-cols-1 md:grid-cols-12 lg:grid-cols-12 gap-2 container mx-auto w-full'>

                <div className="col-span-12 rounded-lg p-4">

                    <div className="flex py-2 justify-center items-center">
                        <div className="flex-1 flex pl-2 font-bold text-2xl text-gray-700"> Payment History - {applicationFullData?.consumer_no}</div>
                        <div className="flex justify-right items-center">
                            <button onClick={() => navigate(`/waterViewConsumer/${id}`)} className={`mr-4 bg-white border border-indigo-500 text-indigo-500 px-4 py-1 shadow-lg hover:scale-105 rounded-sm`} >Consumer Details</button>
                            <button onClick={() => navigate(`/waterViewDemand/${id}`)} className={`mr-4 bg-white border border-indigo-500 text-indigo-500 px-4 py-1 shadow-lg hover:scale-105 rounded-sm`} >Demand Details</button>
                            <button className={`mr-4 bg-indigo-500  text-white px-4 py-1 shadow-lg hover:scale-105 rounded-sm`} >Payment History</button>
                        </div>
                    </div>

                    <div className=''>
                        <div className='py-6 mt-2 bg-white rounded-lg shadow-lg p-4'>
                            <div className="flex space-x-5 pl-4 ">
                                <div className='flex-1'>
                                    <div className='font-semibold text-md'>{applicationFullData?.connection_type ? applicationFullData?.connection_type : "N/A"} </div>
                                    <div className='text-gray-500 text-xs'>Connection Type</div>
                                </div>
                                <div className='flex-1'>
                                    <div className='font-bold text-sm'>{applicationFullData?.ward_name ? applicationFullData?.ward_name : "N/A"}</div>
                                    <div className='text-gray-500 text-xs'>Ward No.</div>
                                </div>
                                <div className='flex-1'>
                                    <div className='font-semibold text-md'>{applicationFullData?.owner_char_type ? applicationFullData?.owner_char_type : "N/A"}</div>
                                    <div className='text-gray-500 text-xs'>Ownership Type</div>
                                </div>
                                <div className='flex-1'>
                                    <div className='font-bold text-sm'>{applicationFullData?.property_type ? applicationFullData?.property_type : "N/A"}</div>
                                    <div className='text-gray-500 text-xs'>Property Type</div>
                                </div>
                                <div className='flex-1'>
                                    <div className='font-bold text-sm'>{applicationFullData?.rule_set ? applicationFullData?.rule_set : "N/A"}</div>
                                    <div className='text-gray-500 text-xs'>Rule Set</div>
                                </div>
                            </div>

                            <div className="flex space-x-10  pl-4 mt-4">
                                <div className='flex-1'>
                                    <div className='font-bold text-sm'>{applicationFullData?.pipeline_type ? applicationFullData?.pipeline_type : "N/A"}</div>
                                    <div className='text-gray-500 text-xs'>Pipeline Type</div>
                                </div>
                                <div className='flex-1'>
                                    <div className='font-bold text-sm'>{applicationFullData?.connection_through ? applicationFullData?.connection_through : "N/A"}</div>
                                    <div className='text-gray-500 text-xs'>Connection Through</div>
                                </div>

                                {applicationFullData?.connection_through == 'SAF' ?
                                    <div className='flex-1'>
                                        <div className='font-semibold text-md'>{applicationFullData?.saf_no ? applicationFullData?.saf_no : "N/A"}</div>
                                        <div className='text-gray-500 text-xs'>SAF No</div>
                                    </div> :
                                    <div className='flex-1'>
                                        <div className='font-semibold text-md'>{applicationFullData?.holding_no ? applicationFullData?.holding_no : "N/A"}</div>
                                        <div className='text-gray-500 text-xs'>Holding No</div>
                                    </div>
                                }
                                <div className='flex-1'>
                                    <div className='font-semibold text-md'>{applicationFullData?.area_sqft ? applicationFullData?.area_sqft : "N/A"}</div>
                                    <div className='text-gray-500 text-xs'>Area (Sqft.)</div>
                                </div>
                                <div className='flex-1'>
                                    <div className='font-bold text-sm' >{applicationFullData?.category ? applicationFullData?.category : "N/A"}</div>
                                    <div className='text-gray-500 text-xs'>Category</div>
                                </div>

                            </div>
                        </div>

                    </div>

                    {isLoading && (<BarLoader />)}
                    {
                        readyMadeListData?.length == 0 &&
                        <div className='text-center mt-10'>
                            <span className='text-red-600 font-bold text-lg px-4 py-2 border border-red-500  shadow-xl '> No Payments Found Yet</span>
                        </div>
                    }
                    {
                        readyMadeListData?.length != 0 &&
                        <div className='mt-10 bg-white shadow-xl  rounded-lg'>

                            <table className='min-w-full leading-normal mt-2'>
                                <thead className='font-bold text-left text-sm bg-white text-gray-900'>
                                    <tr>
                                        <th className="px-2 py-3 border-b border-gray-200  text-xs uppercase text-left">#</th>
                                        <th className="px-2 py-3 border-b border-gray-200  text-xs uppercase text-left">Transaction No</th>
                                        <th className="px-2 py-3 border-b border-gray-200  text-xs uppercase text-left">Payment Mode</th>
                                        <th className="px-2 py-3 border-b border-gray-200  text-xs uppercase text-left">Date</th>
                                        <th className="px-2 py-3 border-b border-gray-200  text-xs uppercase text-left">Type</th>
                                        <th className="px-2 py-3 border-b border-gray-200  text-xs uppercase text-left">Paid For</th>
                                        <th className="px-2 py-3 border-b border-gray-200  text-xs uppercase text-left">Amount</th>
                                        <th className="px-2 py-3 border-b border-gray-200  text-xs uppercase text-left">Action</th>

                                    </tr>
                                </thead>
                                <tbody className="text-sm">


                                    {readyMadeListData?.Consumer?.map((data, index) => (
                                        <tr className="bg-white shadow-lg border-b border-gray-200">
                                            <td className="px-2 py-2 text-sm text-left">{index + 1}</td>
                                            <td className="px-2 py-2 text-sm text-left">{data?.tran_no}</td>
                                            <td className="px-2 py-2 text-sm text-left">{data?.payment_mode}</td>
                                            <td className="px-2 py-2 text-sm text-left">{data?.tran_date}</td>
                                            <td className="px-2 py-2 text-sm text-left">{data?.tran_type}</td>
                                            <td className="px-2 py-2 text-sm text-left">{moment(data?.demand?.demand_from)?.format('MMM/yy')} - {moment(data?.demand?.demand_upto)?.format('MMM/yy')}</td>
                                            <td className="px-2 py-2 text-sm text-left">{data?.amount}</td>
                                            <td className="px-2 py-2 text-sm text-left">
                                                <button onClick={() => navigate(`/waterUserReceipt/${data?.tran_no}`)} type="button" className="cypress_owner_add_update px-2 py-2.5 border border-indigo-500 text-indigo-500 font-medium text-xs leading-tight uppercase rounded shadow-xl hover:bg-indigo-700 hover:text-white hover:shadow-lg  active:shadow-lg transition duration-150 ease-in-out cursor-pointer">View Receipt</button>
                                            </td>

                                        </tr>
                                    ))
                                    }
                                    {readyMadeListData?.connection?.map((data, index) => (
                                        <tr className="bg-white shadow-lg border-b border-gray-200">
                                            <td className="px-2 py-2 text-sm text-left">{index + 1}</td>
                                            <td className="px-2 py-2 text-sm text-left">{data?.tran_no}
                                                <span className='bg-indigo-500 text-white text-xs px-2 rounded-sm ml-2'>Application</span>
                                            </td>
                                            <td className="px-2 py-2 text-sm text-left">{data?.payment_mode}</td>
                                            <td className="px-2 py-2 text-sm text-left">{data?.tran_date}</td>
                                            <td className="px-2 py-2 text-sm text-left">{data?.tran_type}</td>
                                            <td className="px-2 py-2 text-sm text-left">Application</td>
                                            <td className="px-2 py-2 text-sm text-left">{data?.amount}</td>
                                            <td className="px-2 py-2 text-sm text-left">
                                                <button onClick={() => navigate(`/waterConnReceipt/${data?.tran_no}`)} type="button" className="cypress_owner_add_update px-2 py-2.5 border border-indigo-500 text-indigo-500 font-medium text-xs leading-tight uppercase rounded shadow-xl hover:bg-indigo-700 hover:text-white hover:shadow-lg  active:shadow-lg transition duration-150 ease-in-out cursor-pointer">View Receipt</button>
                                            </td>

                                        </tr>
                                    ))
                                    }


                                </tbody>
                            </table>
                        </div>
                    }

                    <div className='mt-10'>
                        <button onClick={() => navigate(`/waterViewDemand/${id}`)} type="button" className="float-right px-6 py-2.5 bg-indigo-500 text-white font-medium text-xs leading-tight uppercase rounded shadow-md hover:bg-indigo-700 hover:shadow-lg focus:bg-indigo-700 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-indigo-800 active:shadow-lg transition duration-150 ease-in-out">Pay</button>
                    </div>
                    <div className='w-full h-40'></div>
                </div>

            </div>
        </>
    )
}

export default WaterViewPaymentHistory